import React, { Component } from 'react';
import { Mutation } from 'react-apollo';
import gql from 'graphql-tag'; 
import Router from 'next/router';
import { ALL_EVENTS_QUERY } from './Calendar';
const DELETE_EVENT_MUTATION = gql`
  mutation DELETE_EVENT_MUTATION($id: ID!) {
    deleteEvent(id: $id) {
      id
    }
  }
`;

class DeleteEvent extends Component {
  update = (cache, update) => {
    // manually update the cache on the client, so it matches the server

    // 1. Read the items in the cachee
  }
  render() {
    return (
      <Mutation mutation={DELETE_EVENT_MUTATION} update={this.update} variables={{id: this.props.id} }>
        {(deleteEvent, {error}) => (
          <button onClick={() => {
            if(confirm("Are you sure you want to delete this event?")) {
              deleteEvent();
              Router.push({
                pathname: '/'
              })
            }
          }}>{this.props.children}</button>
        )
        }
      </Mutation>
    );
  }
}

export { DELETE_EVENT_MUTATION }
export default DeleteEvent;